body {
  margin: 0 auto;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    background-color: #F0F7EE;
}

  img{
      max-height: 200px;
  }

  button{
      cursor: pointer;
  }

  p{
      text-align: center;
      font-size: 1.5em;
  }

  /* helpers */
  .list-item{
    list-style-type: none;
    margin: 10px 0px;
  }
  a {
    text-decoration: none;
    color: black;
  }

  input{
    background-color:1px solid #ced4da;
    border-radius: 10px;
    margin: 0px 10px;
    font-size: 1.2rem;
    padding: 0.75rem;
  }
  /* flex helpers */

  .flex {
    display: -ms-flexbox;
    display: flex;
}

.align-items-center{
  -ms-flex-align: center;
      align-items: center;
}

.justify-space-between{
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.justify-space-around{
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.justify-center{
    -ms-flex-pack: center;
        justify-content: center;
}

.flex-column{
    -ms-flex-direction: column;
        flex-direction: column;
}

.flex-wrap{
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.margin-zero{
    margin: 0px auto;
}

.padding-zero{
    padding: 0px;
}
.text-align-center{
    text-align: center;
}


